<template>
	<view>
		<!-- <view class="banner" v-if="isShow">
			<image class="banner-img"  :src="dataForm.articleCover"></image>
			<view class="banner-title">{{dataForm.cmsName}}</view>
		</view>
		<view class="article-meta" v-if="isShow">
			<text class="article-author">{{dataForm.author}}</text>
			<text class="article-text" style="line-height: 42rpx;">发表于</text>
			<text class="article-time">{{dataForm.pubDate}}</text>
		</view>
		<view class="label" v-if="dataForm.cmsLabel !=''" style="margin: 10rpx 20rpx 10rpx 40rpx;">
			<u-tag :text="item" v-for="(item,index) in labelArr" plain style="margin-right: 20rpx;"> </u-tag>
		</view> -->
		<view class="article-content" v-if="isShow">
			<web-view ref="web" :src="url"></web-view>
			<!-- <u-read-more show-height="700" toggle>
				<rich-text :nodes="dataForm.cmsContent" style="white-space: pre-wrap"></rich-text>
			</u-read-more> -->
			
		</view>
		
	</view>
</template>

<script>
	 
	export default {
		data() {
			return {
				url:'',
				title: '',
				labelArr:[],
				dataForm: {
					cmsLabel:''
				},
				cmsId:'',
				isShow:false,
			}
		},
		onLoad(obj) {
			this.cmsId = obj.id
			this.isShow = false
			this.getDetail();
			
			
			
		},

		
		methods: {
			getDetail() {
				this.request({
					url: "/menu/cms/listOne",
					data:{
						cmsId:this.cmsId
					}
				}).then(data => {
					if (data && data.resphead && data.resphead.succeed) {
						this.dataForm = data.respbody
						this.isShow = true
						this.dataForm.pubDate =  this.moment(this.dataForm.pubDate).format('yyyy-MM-DD HH:mm:SS')
						if(this.dataForm.cmsType == 1){
							this.url = this.dataForm.cmsUrl
							// //#ifdef APP-PLUS
							// plus.runtime.openURL(this.url)
							// // #endif
							// //#ifdef H5
							// location.href=this.url
							// // #endif
						}else if(this.dataForm.cmsType == 2){
							this.url = `/hybrid/html/pdf/local.html?type=1&url=${decodeURIComponent(this.dataForm.articlePdf)}`;
						}
						
						this.$nextTick(function(){
							//setTimeout(()=>{
								// var web = document.getElementsByTagName('iframe')[0]
								// web.style.top = '600px'
								// console.log('web',web,web.style.top)
								// var pages = getCurrentPages();
								// var page = pages[pages.length - 1];
								// console.log('this.$refs.web',page)
							//},10)
							
						})
						//console.log('cms',this.dataForm.cmsLabel)
						if(this.dataForm.cmsLabel){
							this.labelArr = this.dataForm.cmsLabel.split(',')
						}
						uni.setNavigationBarTitle({
							title: this.dataForm.cmsName || '文章阅览'
						});
						
						//console.log('dataForm',this.dataForm)
					} else {
						this.isShow = false
						uni.showToast({
							title: data.resphead.message || "获取文章详情失败",
							icon: "none"
						});
					}
				});
			},
			escape2Html(str) {
			    var arrEntities = { 'lt': '<', 'gt': '>', 'nbsp': ' ', 'amp': '&', 'quot': '"' };
			    return str.replace(/&(lt|gt|nbsp|amp|quot);/ig, function (all, t) { return arrEntities[t]; })
			  },
		}
	}
</script>
<style>
	iframe{
		top:624rpx !important ;
	}
</style>
<style lang="scss" scoped>
	/deep/ iframe{
		top:624rpx !important ;
	}
	.banner {
		height: 360rpx;
		overflow: hidden;
		position: relative;
		background-color: #ccc;
	}

	.banner-img {
		width: 100%;
	}

	.banner-title {
		overflow: hidden;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		font-size: 32rpx;
		font-weight: 400;
		line-height: 42rpx;
		white-space: pre-wrap;
		color: white;
		z-index: 11;
		padding: 30rpx;
		background: rgba($color: #000000, $alpha: 0.5);
	}

	.article-meta {
		padding: 20rpx 40rpx;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		color: gray;
	}

	.article-text {
		font-size: 26rpx;
		line-height: 50rpx;
		margin: 0 20rpx;
	}

	.article-author,
	.article-time {
		font-size: 30rpx;
	}

	.article-content {
		padding: 0 30rpx;
		overflow: hidden;
		font-size: 30rpx;
		margin-bottom: 30rpx;
	}
</style>
